import React, { useState } from 'react';
import { Badge, Box, Typography } from '@mui/material';

import i18n from 'i18n';
import { useStyles } from './styles';
import { IPlayerWebRTCProps, TStreamMode } from 'types/player';

type IColor = 'default' | 'success' | 'primary' | 'secondary' | 'error' | 'info' | 'warning';

interface IDotWithText {
  color?: IColor;
  text: string;
  id: TStreamMode;
  changeStreamMode: () => void;
}

function DotWithText(props: IDotWithText) {
  const { id, color, text, changeStreamMode } = props;
  const [optionId, setOptionId] = useState('');
  const classes = useStyles();

  const handleClick = () => {
    if (optionId === id) return;

    setOptionId(id);
    return changeStreamMode();
  };

  return (
    <Box onClick={handleClick} className={classes.root}>
      <Badge
        anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
        overlap="circular"
        color={color}
        variant="dot"
        classes={{ badge: !color ? classes.disabledBadge : undefined }}
      />
      <Typography variant="body2" ml={1} color="white">
        {text}
      </Typography>
    </Box>
  );
}

function ModeOptions(props: any) {
  const { controlBar } = props as IPlayerWebRTCProps;
  const { t } = i18n;

  if (!controlBar?.modeOptions) return null;

  const { changeStreamMode } = controlBar.modeOptions;

  return (
    <Box display="flex" alignItems="center">
      <DotWithText text={t('controlBar.mode.record')} id="playback" changeStreamMode={changeStreamMode} />
      <DotWithText text={t('controlBar.mode.live')} id="live" color="error" changeStreamMode={changeStreamMode} />
    </Box>
  );
}

export default ModeOptions;
