import { IRequestMessage, IResponseMessage, TViewerResponseStatus } from 'types/message';
import { IPlayerWebRTCActions } from 'types/player';
import { debugLog, initDebugLog } from 'utils/log';
import RTC from './rtc';

import { store } from 'components/player_webrtc/redux_store/store';
import { wsOpenSuccess } from 'components/player_webrtc/redux_store/webrtc/slice';

interface ISignalingConnect {
  serverAddress: string;
  debug?: boolean;
}

class Signaling {
  ws?: WebSocket;
  rtcList: Map<string, RTC>;
  timeoutId?: NodeJS.Timeout;
  disconnectNumber: number;
  debug?: boolean;
  actions?: IPlayerWebRTCActions;

  constructor() {
    this.rtcList = new Map<string, RTC>();
    this.disconnectNumber = 0;
    this.debug = false;
  }

  connect = async ({serverAddress, debug}: ISignalingConnect) => {
    try {
      this.debug = debug;
      initDebugLog(debug);

      // this.actions = actions;

      debugLog({ '[Signaling.connect]': 'wss://dev.danateq.vn/signaling' });
      this.ws = new WebSocket(serverAddress);

      this.ws.onopen = this.wsOnOpen;
      this.ws.onmessage = this.wsOnMessage;
    } catch (error) {
      debugLog({ '[Signaling.connectError]': error });
    }
  };

  wsAuth = async (token: string) => {
    this.sendMessage({
      id: 'authen',
      token,
    });
  };

  wsOnOpen = async () => {
    // await this.wsAuth();

    console.log('wsOpen 111');

    // this.actions?.wsOpenSuccess();

    // const [rtc] = Array.from(this.rtcList.values());
    // rtc?.initTrack(null);
  };

  wsOnMessage = (message: any) => {
    const parsedMessage: IResponseMessage = JSON.parse(message.data);
    const viewerId = parsedMessage.viewerId as string;

    const rtc = this.rtcList.get(viewerId);

    switch (parsedMessage.id) {
      case 'authen':
        // console.log({actions: this.actions})
        if (parsedMessage.authenStatus === 'success') {
          store.dispatch(wsOpenSuccess());
          // this.actions?.wsOpenSuccess();
        }
        break;
      case 'camera':
        debugLog({ '[Signaling.wsOnMessage.case.camera]': rtc });
        if (!rtc) return;
        rtc.createPcPeerConnection();
        break;
      case 'viewerAccepted':
        if (!rtc) return;
        this.viewerResponse(parsedMessage, 'accepted');
        break;
      case 'viewerRejected':
        if (!rtc) return;
        this.viewerResponse(parsedMessage, 'rejected');
        break;
      case 'iceCandidate':
        if (!rtc) return;
        if (parsedMessage.iceCandidate) {
          const candidate = parsedMessage.iceCandidate;
          if (candidate) {
            if (!rtc.pc) return;
            if (rtc.pc.remoteDescription) {
              rtc.pc.addIceCandidate({
                candidate: candidate.candidate,
                sdpMid: candidate.sdpMid,
                sdpMLineIndex: candidate.sdpMLineIndex,
              });
            } else {
              rtc.iceCandidateQueue.push(candidate);
            }
          }
        }
        break;
      case 'viewerError':
        debugLog({ '[Signaling.wsOnMessage.viewerError]': { parsedMessage, rtc } });
        if (!rtc) return;
        rtc.actions.setError({ message: parsedMessage.error });
        break;
      case 'signalingError':
        debugLog({ '[Signaling.wsOnMessage.signalingError]': { parsedMessage } });
        // this.actions?.setError({ message: parsedMessage.error });
        break;
      default:
        console.warn('unrecognized message', parsedMessage);
        break;
    }
  };

  addRtc = (viewerId: string, rtc: RTC) => {
    this.rtcList.set(viewerId, rtc);
  };

  sendMessage = (message: IRequestMessage) => {
    const jsonMessage = JSON.stringify(message);
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      this.ws.send(jsonMessage);
    } else {
      console.warn('ws.readyState != WebSocket.OPEN');
    }
  };

  viewerResponse = (message: IResponseMessage, status: TViewerResponseStatus) => {
    if (status === 'rejected') {
      const errorMsg = message.error || ' Unknown error';
      console.warn('viewerResponse rejected: ', errorMsg);

      return this.dispose(message.viewerId);
    }

    if (status === 'accepted') {
      if (!message.sdpAnswer) return;
      const sdpAnswer = message.sdpAnswer;
      const viewerId = message.viewerId as string;

      const rtc = this.rtcList.get(viewerId);
      return rtc?.acceptOffer('answer', sdpAnswer);
    }
  };

  dispose = (viewerId?: string) => {
    debugLog({ '[Signaling.dispose]': viewerId });
    this.close();
    if (!viewerId) return;
    const rtc = this.rtcList.get(viewerId);
    rtc?.close();
  };

  close = () => {
    debugLog({ '[Signaling.close] closed': this.timeoutId });
    if (this.timeoutId) {
      clearTimeout(this.timeoutId);
      this.disconnectNumber = 0;
      this.timeoutId = undefined;
    }

    this.rtcList.clear();

    if (!this.ws || this.ws.readyState === WebSocket.CLOSED) return;
    this.ws.close();
  };
}

export const signaling = new Signaling();
export default Signaling;
