import React, { Component } from 'react';
import { CameraStatusEnum, IPlayerWebRTCProps } from 'types/player';

import VideoStatus from './status';
import { toastMessage } from 'utils/common';
import { debugLog } from 'utils/log';
import { VideoStyled, VideoWrapper } from './styles';
import { signaling } from '../controller/signaling';
import RTC from '../controller/rtc';
import { store } from 'components/player_webrtc/redux_store/store';

const { NORMAL, WARNING } = CameraStatusEnum;

export default class Video extends Component<any> {
  video: HTMLVideoElement | null;
  rtc: RTC = new RTC({
    actions: this.props.actions,
    cameraId: this.props.cam.id,
    isSupportAudio: this.props.cam.isSupportAudio,
  });
  rtcAudio: RTC | null = null;

  constructor(props: IPlayerWebRTCProps) {
    super(props);
    this.video = null;

    // this.init();
    // this.rtc =
    //   props.cam.status === ERROR || props.cam.status === OFFLINE
    //     ? null
    //     : new RTC({ cameraId: props.cam.id, actions: props.actions, isSupportAudio: props.cam.isSupportAudio });
  }

  get actions() {
    const { actions: newActions } = this.props as IPlayerWebRTCProps;
    return newActions;
  }

  get player() {
    const { player: newPlayer } = this.props as IPlayerWebRTCProps;
    return newPlayer;
  }

  set muted(val: boolean) {
    if (!this.video) return;
    this.video.muted = val;
  }

  componentDidMount() {
    console.log('componentDidMount', signaling?.ws?.readyState === WebSocket.OPEN);
    // console.log('componentDidMount', signaling.ws?.readyState);
    // if (!signaling.ws) return toastMessage.error('cannot connect signaling server');
    // if (signaling.ws.readyState === WebSocket.OPEN) return this.startPlayer();
    this.startPlayer()
  }

  componentDidUpdate(prevProps: IPlayerWebRTCProps) {
    console.log('componentDidMount', signaling?.ws?.readyState === WebSocket.OPEN);
    // if (prevProps.player.isAudioStarting !== this.props?.player?.isAudioStarting) {
    //   if (this.props?.player?.isAudioStarting) {
    //     this.rtcAudio = new RTC({ cameraId: this.props.cam.id, actions: this.actions, isSupportAudio: true });
    //     this.startPlayer(this.rtcAudio.viewerId, this.rtcAudio);
    //   } else {
    //     if (!this.rtcAudio) return;
    //     this.rtcAudio.toggleAudio('stopAudio');
    //     this.rtcAudio.close();
    //     this.rtcAudio.stopAudioStream();
    //   }
    // }


    console.log({ prevProps: prevProps.player, props: this.props.player });

    if (this.props?.player.error && prevProps.player.error !== this.props.player.error) {
      // this.stopPlayer();
    }

    if (this.props.player.isWsOpen && prevProps.player.isWsOpen !== this.props.player.isWsOpen) {
      // this.startPlayer();
      // if (this.props?.player.error) {
      //   this.stopPlayer();
      // } else {
      //   console.log('componentDidUpdate')
      //   this.startPlayer();
      // }
      // console.log('this.props.player.isWsOpen', this.props.player.isWsOpen);
    }
  }

  componentWillUnmount(): void {
    this.stopPlayer();
  }

  startPlayer = (viewerId?: string, rtc?: RTC) => {
    if (!this.rtc) return toastMessage.error('not found RTC');
    signaling.addRtc(viewerId || this.rtc.viewerId, rtc || this.rtc);

    if (rtc) {
      return rtc.initTrack(this.video);
    } else {
      return this.rtc.initTrack(this.video);
    }
  };

  stopPlayer = () => {
    if (!this.rtc) return;
    debugLog({ '[Video.stopPlayer]': this.rtc });

    this.rtc.close();
    this.rtcAudio?.close();
    this.rtcAudio?.stopAudioStream();

    return this.handlePause();
  };

  handleCanPlay = () => {
    if (!this.video) return;
    return this.actions.canPlay();
  };

  handlePlaying = () => {
    if (!this.video) return;
    return this.actions.playing();
  };

  handlePlay = () => {
    if (!this.rtc?.pc) return;
    if (!this.video) return toastMessage.error('Not found video');
    this.actions.play();
    return this.video.play();
  };

  handlePause = () => {
    if (!this.video) return toastMessage.error('Not found video');
    if (this.player.paused) return;

    this.actions.pause();
    return this.video.pause();
  };

  handleCanPlayThrough = () => {
    if (!this.video) return;
    return this.actions.canPlayThrough();
  };

  handleError = () => {
    debugLog('[CPN.video]: handle error');
  };

  renderContent() {
    const {
      cam: { status },
      player: { error, isMuted = false },
      // wsOpen = false,
    } = this.props as IPlayerWebRTCProps;

    console.log({ state: store.getState().playerSlice });

    if ((status === NORMAL || status === WARNING) && !error)
      return (
        <VideoStyled
          width="100%"
          height={220}
          ref={(c) => (this.video = c)}
          autoPlay
          muted={isMuted}
          onError={this.handleError}
          onPlay={this.handlePlay}
          onPlaying={this.handlePlaying}
          onCanPlay={this.handleCanPlay}
          onPause={this.handlePause}
          onCanPlayThrough={this.handleCanPlayThrough}
        />
      );

    return <VideoStatus status={WARNING} error={error} wsOpen={true} />;
  }

  render() {
    return <VideoWrapper>{this.renderContent()}</VideoWrapper>;
  }
}
