import React from 'react';
import { Typography } from '@mui/material';

import { VideoWrapper } from '../styles';
import { CameraStatusEnum } from 'types/player';

function VideoStatus({ status, error }: { status: CameraStatusEnum; error?: { message: string } }) {
  let des = '';

  if (status === CameraStatusEnum.ERROR) {
    des = 'Camera ang li';
  }

  if (status === CameraStatusEnum.OFFLINE) {
    des = 'Camera ang tt';
  }

  if (error) {
    des = error.message;
  }

  return (
    <VideoWrapper>
      <Typography
        position="absolute"
        top="50%"
        left="50%"
        color="white"
        variant="body2"
        sx={{
          transform: 'translate(-50%, -50%)',
        }}
      >
        {des}
      </Typography>
    </VideoWrapper>
  );
}

export default VideoStatus;
