import { createSlice } from '@reduxjs/toolkit';

import { IPlayerWebRTCSlice } from 'types/player';

const initialState: IPlayerWebRTCSlice = {
  paused: true,
  isFullscreen: false,
  waiting: false,
  isWsOpen: false,
  isMuted: true,
  isAlarm: false,
  isAudioStarting: false,
  isAudioNotSupport: false,
  error: null,
};

const playerSlice = createSlice({
  initialState,
  name: 'playerSlice',
  reducers: {
    changeFullscreen: (state, action) => {
      state.isFullscreen = action.payload;
    },
    play: (state) => {
      state.paused = false;
      state.waiting = false;
    },
    playing: (state) => {
      state.waiting = false;
    },
    canPlay: (state) => {
      state.waiting = false;
    },
    canPlayThrough: (state) => {
      state.waiting = false;
    },
    pause: (state) => {
      state.paused = true;
    },
    waiting: (state) => {
      state.waiting = true;
    },
    mute: (state) => {
      state.isMuted = true;
    },
    unmute: (state) => {
      state.isMuted = false;
    },
    startAlarm: (state) => {
      state.isAlarm = true;
    },
    stopAlarm: (state) => {
      state.isAlarm = false;
    },
    startAudio: (state) => {
      state.isAudioStarting = true;
    },
    stopAudio: (state) => {
      state.isAudioStarting = false;
    },
    setIsAudioNotSupport: (state) => {
      state.isAudioNotSupport = true;
    },
    wsOpenSuccess: (state) => {
      state.isWsOpen = true;
      console.log('state.isWsOpen', state.isWsOpen)
    },
    setError: (state, action) => {
      state.error = action.payload;
      state.waiting = false;
    },
    setPeerConnectionIssue: (state, action) => {
      state.peerConnectionIssue = action.payload;
    },
  },
});

export const { reducer, actions } = playerSlice;
export const { setError } = actions;
export const { wsOpenSuccess } = actions;
export default reducer;
