import React from 'react';
import { VisibilityOffOutlined } from '@mui/icons-material';

import i18n from 'i18n';
import { IPlayerWebRTCProps } from 'types/player';
import ButtonWrapper from '../button_wrapper';

function FollowToggle(props: any) {
  const { controlBar } = props as IPlayerWebRTCProps;

  if (!controlBar?.follow) return null;

  const { unFollow } = controlBar.follow;
  const { t } = i18n;

  const title = t('controlBar.unFollow');

  const handleClick = () => unFollow();

  return <ButtonWrapper title={title} icon={VisibilityOffOutlined} handleClick={handleClick} disablePortal />;
}

export default FollowToggle;
