import * as React from 'react';
import { I18nextProvider } from 'react-i18next';

import i18n from 'i18n';
import PlayerRTC from 'components/player_webrtc';
import { signaling } from 'components/player_webrtc/controller/signaling';
import { ICamProps, IPlayerWebRTCControlBar } from './types/player';
import { toastMessage } from 'utils/common';
import PlayerHls from 'components/player_hls';

export declare interface IPlayerStreamProps {
  type: 'hls' | 'rtc';
  cam: ICamProps;
  controlBar?: IPlayerWebRTCControlBar;
}

interface ISignalingConfig {
  token: string;
  serverAddress: string;
}

export const useSignaling = ({ token, serverAddress }: ISignalingConfig) => {
  const [wsOpen, setWsOpen] = React.useState(false);

  React.useEffect(() => {
    signaling.connect({ serverAddress, debug: true });
    if (signaling.ws) {
      signaling.ws.onopen = async () => {
        await signaling.wsAuth(token);
        setWsOpen(true);
      };
    }
  }, []);

  return { wsOpen };
};

export const initI18n = (locale: string) => {
  i18n.changeLanguage(locale);
};

export const PlayerStream = (props: IPlayerStreamProps) => {
  const renderContent = () => {
    if (props.type === 'hls') return <PlayerHls {...props} />;
    if (props.type === 'rtc') return <PlayerRTC {...props} />;

    return toastMessage.error('Loi khng hp l');
  };
  return (
    <I18nextProvider i18n={i18n}>
      <h3>{props.type}</h3>

      {renderContent()}
    </I18nextProvider>
  );
};
