import React from 'react';
import { VolumeOffOutlined, VolumeUpOutlined } from '@mui/icons-material';

import i18n from 'i18n';
import { IPlayerWebRTCProps } from 'types/player';
import { toastMessage } from 'utils/common';
import ButtonWrapper from '../button_wrapper';

function Volume(props: any) {
  const { actions, player, manager, cam, controlBar } = props as IPlayerWebRTCProps;
  const { t } = i18n;

  if (!cam.isEnabledSpeaker || !controlBar?.showVolume) return null;

  const title = player.isMuted ? t('controlBar.volume.off') : t('controlBar.volume.on');

  const handleClick = async () => {
    const { video } = manager;
    if (player.error || !video?.rtc?.pc) return;

    if (player.isMuted) {
      if (!cam.isSupportAudio) return toastMessage.error(t('controlBar.audio.isNotSupported'));
      actions.unmute();
      video.muted = false;
    } else {
      video.muted = true;
      actions.mute();
    }

    return;
  };

  return (
    <ButtonWrapper
      title={title}
      icon={player.isMuted ? VolumeOffOutlined : VolumeUpOutlined}
      handleClick={handleClick}
      disablePortal
    />
  );
}

export default Volume;
