import React from 'react';
import { RefreshRounded } from '@mui/icons-material';

import i18n from 'i18n';
import { IPlayerWebRTCProps } from 'types/player';
import { CameraStatusEnum } from 'types/player';
import ButtonWrapper from '../button_wrapper';

function Refresh(props: any) {
  const {
    manager: { video },
    player,
    actions,
    cam,
    controlBar,
    
  } = props as IPlayerWebRTCProps;

  if (cam.status === CameraStatusEnum.ERROR || cam.status === CameraStatusEnum.OFFLINE || controlBar?.hideRefresh)
    return null;

  const { t } = i18n;

  const handleClick = () => {
    if (!video || player.waiting) return;

    if (player.error) {
      actions.setError(null);
    }

    video.startPlayer();
  };

  const title = t('controlBar.refresh');

  return <ButtonWrapper title={title} icon={RefreshRounded} handleClick={handleClick} disablePortal />;
}

export default Refresh;
