import React from 'react';
import { ControlCamera } from '@mui/icons-material';

import i18n from 'i18n';
import { toastMessage } from 'utils/common';
import { IPlayerWebRTCProps } from 'types/player';
import ButtonWrapper from '../button_wrapper';

function PTZToggle(props: any) {
  const { cam, controlBar } = props as IPlayerWebRTCProps;
  const { t } = i18n;

  if (!controlBar?.ptz || !cam.isEnabledPTZ) return null;

  const { togglePTZ } = controlBar.ptz;

  const handleClick = () => {
    if (!cam.isSupportPtz) return toastMessage.error(t('controlBar.ptz.isNotSupported'));
    togglePTZ();
    return;
  };

  const title = t('controlBar.ptz.isSupported');

  return (
    <ButtonWrapper title={title} icon={ControlCamera} handleClick={handleClick} placement="top-start" disablePortal />
  );
}

export default PTZToggle;
