import { Store } from '@reduxjs/toolkit';

import Video from 'components/player_webrtc/video';

export type TStreamMode = 'live' | 'playback';

export enum CameraStatusEnum {
  NORMAL = 'normal',
  WARNING = 'warning',
  ERROR = 'error',
  OFFLINE = 'offline',
}

export interface IManager {
  rootElement: HTMLElement;
  store: Store;
  video: Video | null;
}

export interface IActions {
  snapshot: () => void;
  toggleFullscreen: (rootEl: HTMLElement | null, player: IPlayerWebRTCSlice) => void;
  changeFullscreen: (isFullscreen: boolean) => void;
  play: () => void;
  pause: () => void;
  waiting: () => void;
  playing: () => void;
  canPlay: () => void;
  canPlayThrough: () => void;
  setError: (payload: { message: string; detail?: string } | null) => void;
}

export interface IPlayerHlsActions extends IActions {
  playToggle: () => void;
  seeking: () => void;
  seeked: () => void;
  playing: () => void;
  controlBarActive: (active: boolean) => void;
}

export interface IPlayerWebRTCActions extends IActions {
  mute: () => void;
  unmute: () => void;
  startAlarm: () => void;
  stopAlarm: () => void;
  startAudio: () => void;
  stopAudio: () => void;
  setIsAudioNotSupport: () => void;
  setPeerConnectionIssue: (payload: number) => void;
  wsOpenSuccess: () => void;
}

export interface IPlayerWebRTCSlice {
  paused: boolean;
  isFullscreen: boolean;
  waiting: boolean;
  isWsOpen: boolean;
  isMuted: boolean;
  isAudioStarting: boolean;
  isAlarm: boolean;
  isAudioNotSupport: boolean;
  error: {
    message: string;
    detail?: string;
  } | null;
  peerConnectionIssue?: number;
}

export interface IPlayerHlsSlice {
  paused: boolean;
  isFullscreen: boolean;
  waiting: boolean;
  seeking: boolean;
  error: { message: string; detail?: string } | null;
  showControlBar: boolean;
}

export interface ICamProps {
  name: string;
  status: 'normal' | 'warning' | 'offline' | 'error';
  id: string;
  isSupportAudio?: boolean;
  isSupportPtz?: boolean;
  isEnabledMic?: boolean;
  isEnabledPTZ?: boolean;
  isEnabledSpeaker?: boolean;
}

export interface IPlayerWebRTCControlBar {
  hide?: boolean;
  hidePlay?: boolean;
  hideRefresh?: boolean;
  hideSnapshot?: boolean;
  hideFullscreen?: boolean;
  showMic?: boolean;
  showAlarm?: boolean;
  showVolume?: boolean;
  ptz?: {
    togglePTZ: () => void;
  };
  modeOptions?: {
    changeStreamMode: () => void;
  };
  follow?: {
    unFollow: () => void;
  };
  theaterMode?: {
    isTheaterMode: boolean;
    toggleTheaterMode: () => void;
  };
}

export interface IPlayerProps {
  manager: IManager;
  cam: ICamProps;
  actions: IPlayerHlsActions;
  player: IPlayerWebRTCSlice | IPlayerHlsSlice;
  controlBar?: IPlayerWebRTCControlBar;
  order?: number;
  key?: string;
  store: Store;
}

export interface IPlayerWebRTCProps {
  manager: IManager;
  actions: IPlayerWebRTCActions;
  player: IPlayerWebRTCSlice;
  cam: ICamProps;
  store: Store;
  isTheaterMode?: boolean;
  controlBar?: IPlayerWebRTCControlBar;
  order?: number;
  key?: string;
}

