import React from 'react';
import { Pause, PlayArrow } from '@mui/icons-material';

import i18n from 'i18n';
import { IPlayerWebRTCProps } from 'types/player';
import ButtonWrapper from '../button_wrapper';

function PlayToggle(props: any) {
  const { t } = i18n;

  const { player, manager, controlBar } = props as IPlayerWebRTCProps;

  if (controlBar?.hidePlay) return null;

  const title = player.paused ? t('controlBar.play.false') : t('controlBar.play.true');

  const handleClick = () => {
    const { video } = manager;

    if (player.error || !video) return;
    // if (player.error || !video || !video?.rtc?.pc) return;

    if (player.paused) {
      video.handlePlay();
    } else {
      video.handlePause();
    }
  };

  return (
    <ButtonWrapper
      title={title}
      icon={player.paused ? PlayArrow : Pause}
      handleClick={handleClick}
      placement="top-start"
      disablePortal
    />
  );
}

export default PlayToggle;
