import { Store, configureStore } from '@reduxjs/toolkit';

import reducer from 'components/player_webrtc/redux_store/root_reducer';
import * as otherActions from 'components/player_webrtc/redux_store/webrtc/action';
import { IPlayerWebRTCActions } from 'types/player';
import { actions as playerActions } from 'components/player_webrtc/redux_store/webrtc/slice';
import { RootState } from 'components/player_webrtc/redux_store/store';
import Video from './video';

export default class Manager {
  store: Store;
  video: Video | null;
  rootElement: HTMLElement | null;

  constructor() {
    this.store = configureStore({ reducer, devTools: false });
    this.video = null;
    this.rootElement = null;
  }

  getState(): RootState {
    return this.store.getState();
  }

  getActions() {
    const { dispatch } = this.store;
    const actions = Object.assign({}, playerActions, otherActions);

    const bindActionCreator = (actionCreator: any) => {
      const self = this;
      return function bindAction(...args: any) {
        const action = actionCreator.apply(self, args);
        if (typeof action !== 'undefined') dispatch(action);
      };
    };

    return Object.keys(actions)
      .filter((key) => typeof actions[key as keyof IPlayerWebRTCActions] === 'function')
      .reduce((boundActions, key) => {
        boundActions[key as keyof IPlayerWebRTCActions] = bindActionCreator(actions[key as keyof IPlayerWebRTCActions]);
        return boundActions;
      }, {} as IPlayerWebRTCActions);
  }

  subscribeToStateChange(listener: (state: any, preState: any) => void, getState: () => void) {
    if (!getState) {
      getState = this.getState.bind(this);
    }

    let preState = getState();

    const handleChange = () => {
      const state = getState();
      if (state === preState) return;

      const prevStateCopy = preState;
      preState = state;
      listener(state, prevStateCopy);
    };

    this.store.subscribe(handleChange);
  }

  subscribeToPlayerStateChange(listener: () => void) {
    return this.subscribeToStateChange(listener, () => this.getState().playerSlice);
  }
}
