import React from 'react';
import { CameraAltOutlined } from '@mui/icons-material';

import i18n from 'i18n';
import { IPlayerWebRTCProps } from 'types/player';
import ButtonWrapper from '../button_wrapper';

function Snapshot(props: any) {
  const { actions, player, manager, controlBar } = props as IPlayerWebRTCProps;
  const { video } = manager;
  const { t } = i18n;

  const title = t('controlBar.snapshot');

  const handleClick = () => {
    if (player.error || !video) return;
    // if (player.error || !video?.rtc?.pc) return;
    actions.snapshot();
  };

  if (controlBar?.hideSnapshot) return null;

  return <ButtonWrapper title={title} icon={CameraAltOutlined} handleClick={handleClick} />;
}

export default Snapshot;
