import React, { useState } from 'react';
import { Refresh } from '@mui/icons-material';
import { Box, Typography, CircularProgress, IconButton } from '@mui/material';

import { CameraStatusEnum } from 'types/player';

interface VideoStatusProps {
  status: CameraStatusEnum;
  wsOpen?: boolean;
  error: {
    message: string;
    detail?: string;
  } | null;
}

function VideoStatus(props: VideoStatusProps) {
  const { status, error, wsOpen } = props;
  const [loading, setLoading] = useState(false);

  let des = '';
  let reconnect = false;

  if (!wsOpen) {
    des = 'Khng th kt ni ti my ch';
    reconnect = true;
  }

  if (status === CameraStatusEnum.ERROR) {
    des = 'Camera ang li';
    reconnect = false;
  }

  if (status === CameraStatusEnum.OFFLINE) {
    des = 'Camera ang tt';
    reconnect = false;
  }

  if (error) {
    des = error.message;
  }

  const handleSignalingReConnect = async () => {
    setLoading(true);
    setTimeout(() => {
      //   dispatch(setWsReconnection(true));
      setLoading(false);
    }, 3200);
  };

  return (
    <Box
      position="absolute"
      top="50%"
      left="50%"
      sx={{
        transform: 'translate(-50%, -50%)',
      }}
      textAlign="center"
    >
      {loading ? (
        <React.Fragment>
          <Typography color="white" variant="body2" mb={3}>
            ang kt ni n my ch
          </Typography>
          <CircularProgress style={{ color: 'white' }} size={32} />
        </React.Fragment>
      ) : (
        <React.Fragment>
          <Typography color="white" variant="body2" mb={2}>
            {des}
          </Typography>
          {!wsOpen && reconnect && (
            <IconButton onClick={handleSignalingReConnect}>
              <Refresh style={{ color: 'white' }} fontSize="large" />
            </IconButton>
          )}
        </React.Fragment>
      )}
    </Box>
  );
}

export default VideoStatus;
