import React from 'react';
import { Component } from 'react';

import { ICamProps, IManager, IPlayerWebRTCControlBar, IPlayerWebRTCProps, IPlayerWebRTCSlice } from 'types/player';
import { mergeAndSortChildren } from 'utils/video';
import CameraName from 'components/camera_name';
import Loading from 'components/loading';
import ControlBar from 'components/control_bar';
import Shortcut from 'components/shortcut';
import Manager from '../manager';
import Video from '../video';
import { VideoDanaPlayer } from './styles';
// import { signaling } from '../controller/signaling';

// export interface IPlayerRTCProps {
//   cam: ICamProps;
//   // wsOpen: boolean;
//   isTheaterMode?: boolean;
//   isFollowDisabled?: boolean;
//   isHideControlBar?: boolean;
// }

export interface IPlayerRTCProps {
  cam: ICamProps;
  controlBar?: IPlayerWebRTCControlBar;
}

export default class PlayerRTC extends Component<IPlayerRTCProps> {
  manager = new Manager();
  actions = this.manager.getActions();

  constructor(props: IPlayerRTCProps) {
    super(props);
    this.manager.subscribeToPlayerStateChange(this.handleStateChange.bind(this));
  }

  getChildren = (props: IPlayerWebRTCProps) => {
    const { ...propsWithoutChildren } = props;

    const defaultChildren = this.getDefaultChildren();
    return mergeAndSortChildren(defaultChildren, [], propsWithoutChildren);
  };

  getDefaultChildren = () => {
    return [
      <Video
        key="video"
        ref={(c: Video) => {
          this.manager.video = c;
        }}
        order={0.0}
      />,
      <CameraName key="name" order={1.0} />,
      <Loading key="loading" order={2.0} />,
      <ControlBar key="control-bar" order={3.0} />,
      <Shortcut key="shortcut" order={4.0} />,
    ];
  };

  handleStateChange = () => {
    this.forceUpdate();
  };

  render() {
    const playerSlice: IPlayerWebRTCSlice = this.manager.getState().playerSlice;

    const props: IPlayerWebRTCProps = {
      ...this.props,
      player: playerSlice,
      actions: this.actions,
      manager: this.manager as IManager,
      store: this.manager.store,
    };

    const children = this.getChildren(props);

    return (
      <VideoDanaPlayer ref={(c: HTMLElement | null) => (this.manager.rootElement = c)}>{children}</VideoDanaPlayer>
    );
  }
}
