import { v4 as uuidv4 } from 'uuid';
import { IPlayerWebRTCActions } from 'types/player';
import { debugLog } from 'utils/log';
import { signaling } from './signaling';
import { IRequestMessage } from 'types/message';

interface IRTCProps {
  cameraId: string;
  actions: IPlayerWebRTCActions;
  audioStarting?: boolean;
  isSupportAudio?: boolean;
}

let peerConnectionLimit = 0;
let peerConnectionIssue = 0;

const urls = 'turn:10.49.46.162:30478?transport=tcp',
  credential = 'turnpass',
  username = 'turnuser';

const RTCConfig: RTCConfiguration =
  urls && credential && username
    ? {
        iceServers: [
          {
            urls,
            username,
            credential,
          },
        ],
        iceTransportPolicy: 'all',
      }
    : {};

export default class RTC {
  pc: RTCPeerConnection | null = null;
  video: HTMLVideoElement | null = null;
  cameraId: string;
  viewerId: string;
  iceCandidateQueue: any[];
  actions: IPlayerWebRTCActions;
  audioStarting?: boolean;
  isSupportAudio?: boolean;
  peerConnectionTimeoutId?: any;

  constructor(props: IRTCProps) {
    this.cameraId = props.cameraId;
    this.viewerId = uuidv4();
    this.iceCandidateQueue = [];
    this.actions = props.actions;
    this.audioStarting = props.audioStarting;
    this.isSupportAudio = props.isSupportAudio;
    this.peerConnectionTimeoutId = 0;
  }

  initTrack = (video: HTMLVideoElement | null) => {
    debugLog(`RTC.initTrack, cameraId: ${this.cameraId}, viewerId: ${this.viewerId}`);
    this.video = video || this.video;

    signaling.sendMessage({
      id: 'camera',
      cameraId: this.cameraId,
      viewerId: this.viewerId,
    });
  };

  getNetWorkStatus = () => {
    if (!this.pc) return;

    this.pc.getStats().then((stats: RTCStatsReport) => {
      stats.forEach((report) => {
        if (report.type === 'candidate-pair') {
          const { currentRoundTripTime = 0 } = report;

          if (currentRoundTripTime >= 3) {
            peerConnectionIssue += 1;

            if (peerConnectionIssue >= 5) {
              //setError
              peerConnectionIssue = 0;
            }
          }
        }
      });
    });
  };

  startAudioStream = async () => {
    if (!window.localStream) return;
    const localStream: MediaStream = window.localStream;
    localStream.getAudioTracks().forEach((track) => this.pc?.addTrack(track, localStream));
  };

  stopAudioStream = () => {
    if (!window.localStream) return;
    const localStream: MediaStream = window.localStream;
    localStream.getAudioTracks().forEach((track) => track.stop());
  };

  onIceCandidate = (candidate: RTCIceCandidate) => {
    const message: IRequestMessage = {
      id: 'iceCandidate',
      cameraId: this.cameraId,
      viewerId: this.viewerId,
      iceCandidate: candidate,
    };

    signaling.sendMessage(message);
  };

  createPcPeerConnection = async () => {
    try {
      console.log({RTCConfig})
      this.pc = new RTCPeerConnection(RTCConfig);
      debugLog(`[RTC.createPeerConnection]: ${this.pc}`);

      peerConnectionLimit += 1;

      setInterval(this.getNetWorkStatus, 10000);

      if (this.audioStarting) {
        this.startAudioStream();
      }

      this.pc.onicecandidate = (e) => {
        if (!e.candidate) return;
        this.onIceCandidate(e.candidate);
      };

      this.pc.ontrack = (e) => {
        if (!this.video) return;
        this.video.srcObject = e.streams[0];
      };

      this.pc.onicecandidateerror = (ev: any) => {
        debugLog({ '[RTC.onicecandidateerror]': ev });
        this.actions.setError({
          message: ev.errorText || 'Li khng xc nh',
        });
      };

      this.pc.onconnectionstatechange = (ev: any) => {
        let timeout = 0;
        if (this.pc?.iceConnectionState === 'disconnected') {
          debugLog({ '[RTC.onconnectionstatechange]': ev });

          if (this.peerConnectionTimeoutId) return;

          this.peerConnectionTimeoutId = setInterval(() => {
            timeout++;

            //6s
            if (timeout >= 6) {
              this.actions.setError({ message: ' xy ra li khi kt ni n my ch' });
            }
          }, 1000);
        }
      };

      if (!this.audioStarting) {
        this.actions.waiting();
      }

      await this.createOffer();
    } catch (error) {
      debugLog(`[RTC.createPeerConnection]: ${error}`);
      if (error?.message && peerConnectionLimit >= 256) {
        this.actions.setError({ message: 'Cannot create peerConnection', detail: 'peerConnectionLimit' });
        //set err
    }
    }
  };

  

  createOffer = async () => {
    if (!this.pc) return;

    const offerToReceiveAudio = this.audioStarting ? false : this.isSupportAudio || false;
    const offerToReceiveVideo = this.audioStarting ? false : true;

    const offer = await this.pc.createOffer({
      offerToReceiveAudio,
      offerToReceiveVideo,
    });

    const message: IRequestMessage = {
      id: 'startViewer',
      cameraId: this.cameraId,
      viewerId: this.viewerId,
      sdpOffer: offer.sdp,
    };

    signaling.sendMessage(message);
    this.pc.setLocalDescription(offer);
  };

  acceptOffer = async (type: RTCSdpType, sdp: string) => {
    debugLog({ '[RTC.acceptOffer]': this.pc });

    if (!this.pc) return;

    await this.pc.setRemoteDescription({
      type,
      sdp,
    });

    if (this.iceCandidateQueue.length > 0) {
      while (this.iceCandidateQueue.length) {
        const candidate: RTCIceCandidateInit = this.iceCandidateQueue.shift();

        this.pc.addIceCandidate({
          candidate: candidate.candidate,
          sdpMid: candidate.sdpMid,
          sdpMLineIndex: candidate.sdpMLineIndex,
        });
      }
    }

    if (this.audioStarting) {
      this.toggleAudio('startAudio');
    }
  };

  toggleAudio = (id: 'startAudio' | 'stopAudio') => {
    const message: IRequestMessage = {
      id,
      cameraId: this.cameraId,
      viewerId: this.viewerId,
    };

    signaling.sendMessage(message);
  };

  toggleAlarm = (id: 'startAlarm' | 'stopAlarm') => {
    const message: IRequestMessage = {
        id,
        cameraId: this.cameraId,
        viewerId: this.viewerId
    }
    
    signaling.sendMessage(message)
  }

  close = () => {
    debugLog(`RTC.close cameraId: ${this.cameraId}, viewerId: ${this.viewerId}`);

    if (this.peerConnectionTimeoutId) {
      clearInterval(this.peerConnectionTimeoutId);
      this.peerConnectionTimeoutId = undefined;
    }

    if (!this.pc) return;

    this.pc.close();
    this.pc.onicecandidate = null;
    this.pc.ontrack = null;
    this.pc.onnegotiationneeded = null;
    this.pc.onconnectionstatechange = null;
    this.pc.onicegatheringstatechange = null;
    this.pc.onsignalingstatechange = null;
    this.pc = null;

    const message: IRequestMessage = {
      id: 'stop',
      cameraId: this.cameraId,
      viewerId: this.viewerId,
    };

    signaling.sendMessage(message);
  };
}
