import React from 'react';
import { AlarmOffOutlined, AlarmOnOutlined } from '@mui/icons-material';

import { IPlayerWebRTCProps } from 'types/player';
import i18n from 'i18n';
import ButtonWrapper from '../button_wrapper';

function AlarmToggle(props: any) {
  const { actions, player, manager, controlBar } = props as IPlayerWebRTCProps;
  const { t } = i18n;

  if (!controlBar?.showAlarm) return null;

  const title = player.isAlarm ? t('controlBar.alarm.off') : t('controlBar.alarm.on');

  const handleClick = async () => {
    const { video } = manager;
    if (player.error || !video?.rtc?.pc) return;

    if (player.isAlarm) {
      video.rtc.toggleAlarm('stopAlarm');
      actions.stopAlarm();
    } else {
      video.rtc.toggleAlarm('startAlarm');
      actions.startAlarm();
    }
  };

  return (
    <ButtonWrapper title={title} icon={player.isAlarm ? AlarmOnOutlined : AlarmOffOutlined} handleClick={handleClick} />
  );
}

export default AlarmToggle;
