import React from 'react';
import { MicOffOutlined, MicOutlined } from '@mui/icons-material';

import i18n from 'i18n';
import { IPlayerWebRTCProps } from 'types/player';
import { toastMessage } from 'utils/common';
import ButtonWrapper from '../button_wrapper';

function MicToggle(props: any) {
  const { actions, player, manager, cam, controlBar } = props as IPlayerWebRTCProps;
  const { t } = i18n;

  const title = player.isAudioStarting ? t('controlBar.audio.off') : t('controlBar.audio.on');

  const handleClick = async () => {
    const { video } = manager;
    if (player.error || !video?.rtc?.pc) return;

    if (player.isAudioStarting) {
      actions.stopAudio();
    } else {
      if (!cam.isSupportAudio) return toastMessage.error(t('controlBar.audio.isNotSupported'));

      const lcStream = await getAudioDevice();

      if (!lcStream) return;
      actions.startAudio();
    }

    return;
  };

  const getAudioDevice = async () => {
    try {
      const localStream = await navigator.mediaDevices.getUserMedia({
        audio: {
          echoCancellation: true,
        },
      });
      window.localStream = localStream;
      return localStream;
    } catch (error) {
      console.log({ error });
      return toastMessage.error(t('controlBar.audio.isDeviceNotFound'));
    }
  };

  if (!cam.isEnabledMic || !controlBar?.showMic) return null;

  return (
    <ButtonWrapper
      title={title}
      icon={player.isAudioStarting ? MicOutlined : MicOffOutlined}
      handleClick={handleClick}
      disablePortal
    />
  );
}

export default MicToggle;
